.product-container {
  /* display: flex;
  flex-wrap: wrap; */
  /* width: 1000px; */
  /* margin: auto; */
  padding-top: 96px;
}

.product-image-container {
  position: relative;
  max-width: 400px;
  border-radius: 1rem;
  overflow: hidden;
}

.product-curve {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.product-image {
  vertical-align: middle;
  width: 100%;
}

.product-background-name {
  font-size: 320px;
  position: fixed;
  left: -40px;
  top: 400px;
  z-index: -1;
  opacity: 0.2;
  white-space: nowrap;
}

.product-title {
  font-size: 6rem;
  margin: 0 0 48px;
  white-space: nowrap;
}

.product-sub-header {
  font-size: 2.5rem;
  margin: 0 0 48px;
  white-space: nowrap;
}