:root {
  --color-black: #111111;
  --color-blue: #4C88B9;
  --color-brown: #7E6859;
  --color-green: #61A155;
  --color-grey: #444444;
  --color-ice: #BBBBBB;
  --color-orange: #E76510;
  --color-pink: #D09296;
  --color-purple: #6B5B7A;
  --color-red: #E20303;
  --color-white: #FFFFFF;
  --color-yellow: #D09431;
}

@font-face {
  font-family: "Balgend";
  src: url('../assets/Balgend.ttf');
}

@font-face {
  font-family: "Nunito";
  src: url('../assets/nunito/Nunito-Regular.ttf');
}

@font-face {
  font-family: "Hedvig Letters Serif";
  src: url('../assets/Hedvig-Letters-Serif.ttf');
}

body, .nunito {
  margin: 0;
  font-family: "Hedvig Letters Serif", "Times New Roman", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  width: 100vw;
  overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6, .balgend {
  font-family: "Balgend";
}

p {
  line-height: 1.75;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bg {
  background-attachment: fixed;
  background-size: cover;
}

.bg-color-black { background-color: var(--color-black); }
.bg-color-blue { background-color: var(--color-blue); }
.bg-color-brown { background-color: var(--color-brown); }
.bg-color-green { background-color: var(--color-green); }
.bg-color-grey { background-color: var(--color-grey); }
.bg-color-ice { background-color: var(--color-ice); }
.bg-color-orange { background-color: var(--color-orange); }
.bg-color-pink { background-color: var(--color-pink); }
.bg-color-purple { background-color: var(--color-purple); }
.bg-color-red { background-color: var(--color-red); }
.bg-color-white { background-color: var(--color-white); }
.bg-color-yellow { background-color: var(--color-yellow); }

.bg-fill-black { fill: var(--color-black); }
.bg-fill-blue { fill: var(--color-blue); }
.bg-fill-brown { fill: var(--color-brown); }
.bg-fill-green { fill: var(--color-green); }
.bg-fill-grey { fill: var(--color-grey); }
.bg-fill-ice { fill: var(--color-ice); }
.bg-fill-orange { fill: var(--color-orange); }
.bg-fill-pink { fill: var(--color-pink); }
.bg-fill-purple { fill: var(--color-purple); }
.bg-fill-red { fill: var(--color-red); }
.bg-fill-white { fill: var(--color-white); }
.bg-fill-yellow { fill: var(--color-yellow); }

.bg-image-blue { background-image: url(../assets/background-blue.svg); }
.bg-image-brown { background-image: url(../assets/background-brown.svg); }
.bg-image-green { background-image: url(../assets/background-green.svg); }
.bg-image-grey { background-image: url(../assets/background-grey.svg); }
.bg-image-ice { background-image: url(../assets/background-ice.svg); }
.bg-image-orange { background-image: url(../assets/background-orange.svg); }
.bg-image-pink { background-image: url(../assets/background-pink.svg); }
.bg-image-purple { background-image: url(../assets/background-purple.svg); }
.bg-image-red { background-image: url(../assets/background-red.svg); }
.bg-image-white { background-image: url(../assets/background-white.svg); }
.bg-image-yellow { background-image: url(../assets/background-yellow.svg); }

.text-color-black { color: var(--color-black); }
.text-color-blue { color: var(--color-blue); }
.text-color-brown { color: var(--color-brown); }
.text-color-green { color: var(--color-green); }
.text-color-grey { color: var(--color-grey); }
.text-color-ice { color: var(--color-ice); }
.text-color-orange { color: var(--color-orange); }
.text-color-pink { color: var(--color-pink); }
.text-color-purple { color: var(--color-purple); }
.text-color-red { color: var(--color-red); }
.text-color-white { color: var(--color-white); }
.text-color-yellow { color: var(--color-yellow); }

.large-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  margin-bottom: 48px;
}

.small-header {
  min-height: 48px;
  display: flex;
  width: 100vw;
  z-index: 100;
  position: fixed;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  margin-bottom: 48px;
}

.footer {
  padding: 96px 24px;
}

.center-children {
  display: flex;
  align-items: center;
  justify-content: center;
}

.section-header {
  margin-bottom: 48px;
  overflow: hidden;
  width: 100%;
}

.bold {
  font-weight: bold;
}

.section {
  margin: 0 0 48px;
}

.logo-large {
  width: 75vw;
  z-index: 5;
  position: relative;
}

.logo-small {
  height: 1.5rem;
  margin-top: 0.5rem;
}

.header-image {
  background-image: url("../assets/header.png");
  background-size: cover;
}

.header-color {
  height: 100vh;
  width: 100%;
  position: absolute;
  top: 0;
  background-size: cover;
  opacity: 0.7;
  /* z-index: -1; */
}

.margin-0 {
  margin: 0 !important;
}